  fill: currentColor;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: currentColor;
}

.icon-globe {
  width: 1em;
  height: 1em;
  fill: currentColor;
}

.icon-money {
  width: 1.14em;
  height: 1em;
  fill: currentColor;
}

.icon-search {
  width: 1em;
  height: 1em;
  fill: currentColor;
}

.icon-shield {
  width: 0.86em;
  height: 1em;
  fill: currentColor;
}

.icon-touch {
  width: 0.66em;
  height: 1em;
  fill: currentColor;
}

.icon-track {
  width: 1.4em;
