  fill: currentColor;
}

body, .footer__col p, .footer-list__link {
  color: rgba(30, 25, 77, 0.5);
  font-family: "Montserrat", Helvetica, sans-serif;
  font-size: 12px;
  font-weight: 600;
  line-height: 1.917;
}

body {
  background: #f7f7f7;
}

.inner {
  max-width: 1030px;
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
}

.title, .title-upper {
  font-family: "Montserrat", Helvetica, sans-serif;
  color: #1e194d;
  font-size: 15px;
  font-weight: 600;
  line-height: 1.533;
}

.title-upper {
  text-transform: uppercase;
}

.hero-title {
  font-size: 32px;
  font-weight: 700;
  text-transform: uppercase;
  color: #1e194d;
  line-height: 1.2;
}

.block {
  display: flex;
}

.header {
  background-color: #191818;
  padding: 12px 0;
  position: relative;
}

@media only screen and (max-width: 767px) {
  .header {
    padding: 14px 0;
  }
}

.header__wrapper {
  padding: 0 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media only screen and (max-width: 767px) {
  .header__wrapper {
    justify-content: space-between;
  }
}

.header__logo {
  margin-right: auto;
}

@media only screen and (max-width: 767px) {
  .header__logo {
    margin-right: 0;
  }
}

.header__mob-wrap {
  display: flex;
  align-items: center;
}

@media only screen and (max-width: 767px) {
  .header__mob-wrap {
    position: absolute;
    z-index: 20;
    top: 0;
    left: 0;
    min-height: 100vh;
    min-width: 200px;
    padding: 147px 50px 40px;
    transform: translateX(-101%);
    background-color: #fff;
    transition: transform 0.3s, box-shadow 0.3s;
  }
  .is-open .header__mob-wrap {
    transform: translateX(0);
  }
}

.header__burger {
  display: none;
  position: relative;
  z-index: 30;
}

@media only screen and (max-width: 767px) {
  .header__burger {
    display: block;
  }
}

.header__logo {
  position: relative;
  z-index: 100;
}

.header__basket {
  padding: 0 60px 0 40px;
}

.main-nav__list {
  display: flex;
  align-items: center;
}

@media only screen and (max-width: 767px) {
  .main-nav__list {
    flex-direction: column;
  }
}

.main-nav__item {
  padding: 0 40px;
}

@media only screen and (max-width: 767px) {
  .main-nav__item {
    padding: 20px 0;
  }
}

.main-nav__link {
  color: #fff;
  font-size: 0;
  display: block;
}

.no-touch .main-nav__link:hover span:after {
  transform: scaleX(1);
  transform-origin: 0 0;
}

.main-nav__link span {
  font-size: 10px;
  line-height: 1.2;
  font-weight: 500;
  display: inline-block;
  position: relative;
  overflow: hidden;
}

@media only screen and (max-width: 767px) {
  .main-nav__link span {
    font-size: 18px;
  }
}

.main-nav__link span:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  height: 1px;
  width: 100%;
  background-color: #fff;
  transform: scaleX(0);
  transition: transform .4s;
  transform-origin: 100% 0;
}

.logo {
  display: inline-block;
}

.logo__text {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  width: 0;
}

.logo__image {
  display: block;
  max-width: 100%;
}

.logo_big .logo__image {
  height: 35px;
}

.basket-info {
  display: flex;
  align-items: center;
  justify-content: center;
}

.no-touch .basket-info:hover .basket-info__icon {
  transform: scale(1.2);
}

.basket-info__icon {
  font-size: 0;
  margin-right: 5px;
  transform-origin: center;
  transition: transform 0.3s;
}

.basket-info__icon .icon {
  display: inline-block;
  vertical-align: middle;
  color: #fff;
  font-size: 15px;
}

.basket-info__quantity {
  text-align: center;
  display: inline-block;
  vertical-align: top;
  line-height: 18px;
  height: 18px;
  width: 18px;
  border-radius: 50%;
  background-color: #fff;
  color: #191818;
}

.burger {
  width: 16px;
  height: 12px;
  position: relative;
  transform: rotate(0deg);
  transition: .5s ease-in-out;
  cursor: pointer;
}

.burger span {
  display: block;
  position: absolute;
  height: 2px;
  width: 100%;
  opacity: 1;
  left: 0;
  background-color: #fff;
  transform: rotate(0deg);
  transition: transform .15s ease-in-out;
}

.burger span:nth-child(1) {
  top: 0px;
}

.burger span:nth-child(2), .burger span:nth-child(3) {
  top: 5px;
}

.burger span:nth-child(4) {
  top: 10px;
}

.is-open .burger span:nth-child(1) {
  top: 18px;
  width: 0%;
  left: 50%;
}

.is-open .burger span:nth-child(2) {
  transform: rotate(45deg);
}

.is-open .burger span:nth-child(3) {
  transform: rotate(-45deg);
}

.is-open .burger span:nth-child(4) {
  top: 18px;
  width: 0%;
  left: 50%;
}

.footer__col p, .footer-list__link {
  color: rgba(255, 255, 255, 0.5);
  font-weight: 500;
}

.footer {
  background-color: #191818;
  padding: 80px 0;
}

.footer__wrapper {
  font-size: 0;
}

.footer__title {
  color: #fff;
  margin-bottom: 32px;
}

.footer__col {
  display: inline-block;
  width: 25%;
  vertical-align: top;
}

.footer__col_logo {
  text-align: right;
}

.footer__col p {
  margin-bottom: 20px;
}

.footer__form {
  max-width: 180px;
}

.footer-list__item:not(:last-child) {
  margin-bottom: 20px;
}

.hero {
  background-color: #fff;
  margin-bottom: 20px;
}

.hero__inner {
  padding-top: 150px;
  padding-bottom: 191px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: right top;
}

@media only screen and (max-width: 767px) {
  .hero__inner {
    text-align: center;
  }
}

.hero_small .hero__inner {
  padding-top: 96px;
  padding-bottom: 96px;
}

.hero__content {
  display: inline-block;
  padding-left: 160px;
  text-align: center;
}

@media only screen and (max-width: 767px) {
  .hero__content {
    padding-left: 0;
  }
}

.hero__title {
  margin-bottom: 21px;
}

.hero__text {
  color: #1e194d;
  font-size: 18px;
  font-weight: 300;
  line-height: 1.4;
}

.card {
  width: 200px;
  text-align: center;
}

.card__image-wrapper {
  position: relative;
  margin-bottom: 20px;
  border-radius: 5px;
  box-shadow: 0 22px 44px -11px rgba(0, 0, 0, 0.16);
  overflow: hidden;
}

.card__image {
  padding-bottom: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

.card__sale-mark {
  display: inline-block;
  padding: 4px 5px;
  background-color: #52439a;
  font-size: 10px;
  text-transform: uppercase;
  font-weight: 600;
  color: #fff;
  line-height: 1.3;
  position: absolute;
  top: 0;
  left: calc(100% - 9px);
  transform: rotate(90deg);
  transform-origin: 0 0;
}

.card__price {
  color: #1e194d;
  font-size: 20px;
  font-weight: 600;
  line-height: 1.15;
}

.btn-link {
  display: inline-block;
  color: #4d87c6;
  background: none;
  border: none;
  font-family: "Montserrat", Helvetica, sans-serif;
  font-size: 15px;
  font-weight: 600;
  line-height: 1.533;
  transition: color 0.3s;
}

.no-touch .btn-link:hover {
  color: #74a1d2;
}

.search {
  position: relative;
}

.search__input {
  position: absolute;
  right: 0;
  top: 0;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s, visibility 0.3s;
  max-width: 200px;
  background-color: transparent;
  border-bottom: 1px solid #fff;
}

.search__button {
  display: inline-block;
  background-color: transparent;
  font-size: 0;
}

.search__button .icon {
  font-size: 12px;
  color: #fff;
}

.signup-form {
  position: relative;
  width: 100%;
}

.signup-form__input {
  width: 100%;
  border-bottom: 1px solid #e0e0e0;
  background-color: transparent;
  color: #fff;
  padding: 10px 60px 10px 0;
  font-family: "Montserrat", Helvetica, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.2;
  transition: border-bottom-color 0.3s;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.signup-form__input:placeholder {
  color: #a8a6a6;
}

.signup-form__input:focus {
  border-bottom-color: #fff;
}

.signup-form__input:focus + .signup-form__btn {
  color: #74a1d2;
}

.signup-form__btn {
  position: absolute;
  right: 0;
  bottom: 10px;
  display: inline-block;
  background-color: transparent;
  color: #4d87c6;
  font-family: "Montserrat", Helvetica, sans-serif;
  font-weight: 500;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.2;
  transition: color 0.3s;
}

.no-touch .signup-form__btn:hover {
  color: #74a1d2;
}

.feature {
  padding: 30px 50px 30px;
  text-align: center;
  background-color: #fff;
  position: relative;
}

.feature:after {
  z-index: -1;
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  transition: transform 0.4s;
  transform-origin: center;
  box-shadow: 0 10px 24px -10px rgba(0, 0, 0, 0.09);
}

.no-touch .feature:hover:after {
  transform: scale(1.03);
}

.feature__image-wrapper {
  font-size: 0;
}

@media only screen and (max-width: 767px) {
  .feature__image-wrapper {
    margin-bottom: 4px;
  }
}

.feature__image {
  display: inline-block;
  height: 100px;
  width: 100px;
  border-radius: 50%;
  position: relative;
}

.feature__image_orange {
  background-color: #f5881f;
}

.feature__image_blue {
  background-color: #4d87c6;
}

.feature__image_yellow {
  background-color: #f9d009;
}

.feature__image .icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
}

.feature__image .icon-card {
  font-size: 36px;
}

.feature__image .icon-shield {
  font-size: 50px;
}

.feature__image .icon-touch {
  font-size: 44px;
}

.feature__title-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 66px;
  padding: 5px 0;
}

.features {
  margin-bottom: 20px;
}

@media only screen and (max-width: 767px) {
  .features {
    margin-bottom: 16px;
  }
}

.features__wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
  margin: 0 -10px;
}

.features__item {
  flex: 0 0 33.33%;
  min-width: 33.33%;
  padding: 0 10px;
}

.features__item .feature {
  height: 100%;
}

.samples {
  margin-bottom: 40px;
}

.samples__wrapper {
  display: flex;
  border-radius: 5px;
  overflow: hidden;
  background-color: #fff;
}

.samples__descr {
  padding: 124px 28px 24px;
  flex: 0 0 240px;
  min-width: 240px;
  text-align: center;
  background-image: linear-gradient(180deg, #60a8f6 0%, #6ba3f1 6%, #769eea 12%, #8198e3 19%, #8c91dc 26%, #968ad3 34%, #a082cb 42%, #aa79c1 50%, #b370b7 58%, #bb66ad 66%, #c35ba2 74%, #ca4f98 81%, #d1428d 88%, #d73382 94%, #dc2177 100%);
}

.samples__title {
  color: #fff;
}

.samples p {
  color: rgba(255, 255, 255, 0.5);
}

.samples__cards {
  display: flex;
  justify-content: space-around;
  flex-grow: 1;
  padding: 70px 10px 65px;
}
