%font {
  @extend %baseFont;
  color: rgba($white, 0.5);
  font-weight: 500; }

.footer {
  background-color: $blackBG;
  padding: 80px 0;
  &__wrapper {
    font-size: 0; }
  &__title {
    color: $white;
    margin-bottom: 32px; }
  &__col {
    display: inline-block;
    width: 25%;
    vertical-align: top;
    &_logo {
      text-align: right; }
    p {
      @extend %font;
      margin-bottom: 20px; } }
  &__form {
    max-width: 180px; } }

.footer-list {
  &__item {
    &:not(:last-child) {
      margin-bottom: 20px; } }
  &__link {
    @extend %font; } }
