//FONTS
@mixin fontpath($name, $name1, $file) {
  src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
  font-family: $name;
  font-style: $style;
  font-weight: $weight; }

$montserrat: 'Montserrat';

@font-face {
  @include fontdefault(normal, 300, $montserrat);
  @include fontpath('Montserrat-Light', 'Montserrat-Light', Montserrat-Light); }

@font-face {
  @include fontdefault(italic, 300, $montserrat);
  @include fontpath('Montserrat-LightItalic', 'Montserrat-LightItalic', Montserrat-LightItalic); }

@font-face {
  @include fontdefault(normal, 500, $montserrat);
  @include fontpath('Montserrat-Medium', 'Montserrat-Medium', Montserrat-Medium); }

@font-face {
  @include fontdefault(italic, 500, $montserrat);
  @include fontpath('Montserrat-MediumItalic', 'Montserrat-MediumItalic', Montserrat-MediumItalic); }

@font-face {
  @include fontdefault(normal, 600, $montserrat);
  @include fontpath('Montserrat-SemiBold', 'Montserrat-SemiBold', Montserrat-SemiBold); }

@font-face {
  @include fontdefault(italic, 600, $montserrat);
  @include fontpath('Montserrat-SemiBoldItalic', 'Montserrat-SemiBoldItalic', Montserrat-SemiBoldItalic); }


