.feature {
  padding: 30px 50px 30px;
  text-align: center;
  background-color: $white;
  position: relative;
  &:after {
    z-index: -1;
    content: '';
    @include coverdiv;
    background-color: $white;
    transition: transform 0.4s;
    transform-origin: center;
    box-shadow: $lightShadow; }
  @include hover {
    &:after {
      transform: scale(1.03); } }
  &__image-wrapper {
    font-size: 0;
    @include sm {
      margin-bottom: 4px; } }
  &__image {
    display: inline-block;
    @include circle(100);
    position: relative;
    &_orange {
      background-color: $orange; }
    &_blue {
      background-color: $blue; }
    &_yellow {
      background-color: $yellow; }
    .icon {
      @include vcenter;
      color: $white; }
    .icon-card {
      font-size: 36px; }
    .icon-shield {
      font-size: 50px; }
    .icon-touch {
      font-size: 44px; } }
  &__title-wrapper {
    @include flex-center;
    min-height: 66px;
    padding: 5px 0;
 } }    // margin: 0 auto 10px

.features {
  margin-bottom: 20px;
  @include sm {
    margin-bottom: 16px; }
  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    margin: 0 -10px;
